body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, #root{
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
.App {
  text-align: center;
  display: flex;
  width: 100%;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.thirty{
  width: 30%;
  height: 100%;
  background-color: navy;
  display: flex;
  flex-direction: column;
}
.top-ten{
  height: 10%;
  background-color: red;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.top-ten-top, .top-ten-bottom{
  height: 50%;
  width: 100%;
}
.top-ten-top{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.middle-eighty{
  height: 80%;
  background-color: green;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.bottom-ten{
  height: 10%;
  display: flex;
  background-color: yellow;
  justify-content: center;
  flex-direction: column;
}
.weather-img-div, .weather-temp-div, .weather-clim-div{
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
} 
.weather-img{
  height: 150px;
  width: 150px;
}
.seventy{
  width: 70%;
  height: 100%;
  background-color: skyblue;
  display: flex;
  flex-direction: column;
}
.forty{
  height: 40%;
  background-color: #61dafb;
}
.seventy-top-twenty{
  height: 20%;
  background-color: aliceblue;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 32px;
}
.seventy-eighty{
  height: 80%;
  background-color: antiquewhite;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.sixty{
  height: 60%;
  display: flex;
  background-color: aquamarine;
  flex-direction: column;
}
.sixty-top-ten{
  height:  15%;
  background-color: black;
  text-align: center;
  color: white;
}
.sixty-middle-seventy{
  height: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}
.sixty-bottom-thirty{
  height: 15%;
  background-color: blue;
}
.user-list{
  max-height: 500px;
  background: white;
  overflow: auto;
  padding: 3%;
  z-index: 100;
}
.user{
  cursor: pointer;
}
.crosshair{
  cursor: pointer;
}
.five-day-weather{
  width: 15%;
  height: 75%;
  background-color: blueviolet;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.today-div{
  width: 40%;
  height: 40%;
  background-color: cadetblue;
}
.bottom-ten-span-div{
  height: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.location-div{
  height: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.jimbo{
  height: 100px;
  width: 100px;
}
.jimbo-span{
  font-size: 13px
}

div > .user-list{
  z-index: 100
}

